.head-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
}
.head-image{
    width: auto;
    border: 2px dashed black;
    border-radius: 20%;
    transition: max-width 1s,min-width 1s;
}
.head-text{
    font-family:monospace;
    margin: 10px 0 15px 0;
}
.head-image-expanded{
    min-width: 230px;
    max-width: 250px;
}
.head-image-contracted{
    min-width: 60px;
    max-width: 40px;
}

*{
    margin: 0;
    padding: 0;
}
body{
    background-color: blueviolet;
    overflow-x:hidden ;
    color: azure;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    letter-spacing: 2px;
}
.Search-container{
    display: flex;
    justify-content: center;
    margin: 10px;
}
.search-input{
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    background-color: lightcoral;
    border-radius: 50px;
    box-shadow: 0 0 5px white;
    color: aliceblue;
}
.search-input::-webkit-input-placeholder{
    color: aliceblue;
}
.search-input:-ms-input-placeholder{
    color: aliceblue;
}
.search-input::placeholder{
    color: aliceblue;
}
.search-input:focus{
    outline: none;
}
.container{
    display: flex;
    justify-content: center;
    padding: 20px 40px ;
}
.result-name-card{
    color:white;
    background-color: brown;
    margin: 10px 15px;
    min-width: 75px;
    padding: 10px 20px;
    transition: background 0.35s;
    display: flex;
    justify-content: center;
}
.result-name-card:hover{
    background-color: orange;
}
.rescard{
    margin: 0;
    font-weight: 100;
}
